import md5 from '@/md5.js'
export default {
	data() {
		return {
			userInfo: {},
			id: null,
			uid: null,
			book: {},
			isfavor:0,
			comments: [],
			chapters: [],
			list: [{
				name: '详情'
			}, {
				name: '选集'
			}],
			endlist: [{
				name: "全部",
				id: null
			}, {
				name: "连载中",
				id: 0
			}, {
				name: "已完结",
				id: 1
			}, ],
			current: 0

		};
	},
	onLoad: async function(option) { //option为object类型，会序列化上个页面传递的参数
		this.id = option.id;
		this.uid = option.uid;
		this.userInfo = this.$store.state;
		this.getfavor();
		this.getcomicdetails();
		this.getcomments();
		this.getchapters();
	},
	methods: {
		getapi() {
			var timestamp = Date.parse(new Date());
			var api_key = "abcde";
			return [timestamp, md5(api_key + timestamp)];
			//返回api_key+时间戳md5加密
		},
		async getcomicdetails() {
			var key = await this.getapi();
			uni.request({
				url: 'http://www.liaowang.xyz/app/books/detail',
				data: {
					time: key[0],
					token: key[1],
					id: this.id,
					uid: this.uid
				},
				success: (res) => {
					this.book = res.data.book;
					console.log(this.book);
				}
			});
		},
		async getcomments() {
			var key = await this.getapi();
			uni.request({
				url: 'http://www.liaowang.xyz/app/books/getComments',
				data: {
					time: key[0],
					token: key[1],
					book_id: this.id
				},
				success: (res) => {
					this.comments = res.data.comments;
					console.log(res.data);
				}
			});
		},
		async getchapters() {
			var key = await this.getapi();
			uni.request({
				url: 'http://www.liaowang.xyz/app/chapters/getList',
				data: {
					time: key[0],
					token: key[1],
					book_id: this.id
				},
				success: (res) => {
					this.chapters = res.data.chapters;
					console.log(res.data);
				}
			});
		},
		async getfavor() {
			var key = await this.getapi();
			uni.request({
				url: 'http://www.liaowang.xyz/app/users/isfavor',
				data: {
					time: key[0],
					token: key[1],
					book_id: this.id,
					utoken:this.$store.state.utoken
				},
				success: (res) => {
					this.isfavor = res.data.isfavor;
					console.log(res.data);
				}
			});
		},
		gotochapterdetail(id) {
			uni.navigateTo({
				url: '../chapterdetail/chapterdetail?id=' + id
			})
		},
		change(index) {
			this.current = index;
		},
		async collect() {
			var key = await this.getapi();
			uni.request({
				url: 'http://www.liaowang.xyz/app/users/switchfavor',
				data: {
					time: key[0],
					token: key[1],
					utoken: this.$store.state.utoken,
					book_id: this.id,
					isfavor:this.isfavor
				},
				success: (res) => {
					console.log(res.data);
					if(res.data.success===1){
						this.isfavor=res.data.isfavor;
						if(res.data.isfavor===1){
							uni.showToast({
								title: "关注成功",
								duration: 1000
							})
						}
						else{
							uni.showToast({
								title: "取关成功",
								duration: 1000
							})
						}
						
					}
				}
			});
		}
	},
	filters: {
		dateFormat(value) {
			value=value*1000;
			const dt = new Date(value)
			const y = dt.getFullYear()
			const m = (dt.getMonth() + 1 + '').padStart(2, '0')
			const d = (dt.getDate() + '').padStart(2, '0')

			const hh = (dt.getHours() + '').padStart(2, '0')
			const mm = (dt.getMinutes() + '').padStart(2, '0')
			const ss = (dt.getSeconds() + '').padStart(2, '0')

			// return `${y}-${m}-${d} ${hh}:${mm}:${ss}`
			return `${y}-${m}-${d}`
		}
	}
}
